
@import '../_common/h_function';
@import '../_common/h_mixins';

body.fixed{
	//background-color:#000;
	#CPOcontents{
		overflow:hidden;
		height:100%;
		padding-top:0;
	}
	#CPOfooter{
		display:none;
	}
}
form.search_form {
	height: 100%;	
	overflow-x: hidden;
	background:$COL_Light;
}
.h_header_search {
	@include Mix_Icon_Black;
	display: flex;
	background-color: $COL_White;
	height: 53px;
	border-bottom: 1px solid $COL_Secondary;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	transition: all .6s;
	z-index: 600;
	.ic_back {
		position: fixed;
		top: 13px;
		left: 15px;
		margin-top: 0;
	}
	.inp_search {
		padding: 0 10px 0 45px;
		flex: 1 1;
		border: none;
		&.focus + .util .ic_search {
			display: none;
		}
	}
	.util {
		position: relative;
	}
}

.cpo_search{// 검색하기 전체 wrap
	overflow-y:auto;// 메인 스크롤
	height:100%;
	padding-top: 53px;
	.btn_sticky{
		// bottom:-200px !important;
		// transition:bottom 1.0s ease;
		// &.load{
		// 	bottom:0 !important;
		// 	position: sticky;
		// }
		bottom:0 !important;
		position: sticky;
	}	
	&.isAnimate {
		overflow: hidden;
	}
	&.fixed{// 상단 스크롤 UP - cpo_search/fixed
		display: block !important;
		.srch_keyword{// 2. 인기검색어
			position: relative;
			top:0;
			left:unset;
			width:100%;
			// margin-bottom: 25px;
			border-bottom: 1px solid $COL_Line_Dark;
			.list{
				padding-left:21px;
			}
			.btn_open{
				right:11px;
			}
			&.on {
				border-bottom-color: $COL_Line_Medium;
				#srchBest{
					display:none;
				}
				#srchBest2{
					display:block;
				}
			}
		}
		.srch_model{
			top: 25px;
		}
		.srch_recent,
		.srch_auto,
		.srch_promotion_swiper {
			margin-top:0;
		}
	}
	&:not(.fixed) {
		.h_header_search {
			background-color: transparent;
			top: 110px;
			left: 30px;
			right: 30px;
			z-index: 600;
			border-bottom: 2px solid $COL_Primary;
			.inp_search {
				padding-left: 2px;
				.txt {
					background-color: transparent;
				}
			}
			.util {
				padding-right: 0;
			}
		}
		.srch_ifbox {
			@include Boxshadow(0px, 5px, 0.02);
		}
	}
}

