.h_header_search .inp_search::after {
	display: none;
}
.srch_keyword{// 검색하기 2. 인기검색어
	position:fixed;
	top:162px;
	left:30px;
	z-index:599;
	width:calc(100% - 60px);
	height:58px;
	margin:0 auto;
	background:$COL_Light;
	opacity:0;
	transition:top .6s;
	.tit{//인기 검색어 타이틀
		display:none;
	}
	#srchBest2{
		display:none;
	}
	.list{
		overflow:hidden;
		width:100%;
		height:100%;
		padding-left:2px;
		li{
			position:relative;
			a{
				display:block;
				width:100%;
				padding:20px 0 19px;
				@include Mix_Font('Head-R', 14, 18);
				color: $COL_Black;
				em{
					display:inline-block;
					width:20px;
					padding-right: 4px;
					color:$COL_Secondary;
					@include Mix_Font('Head-M', 13, 18);
				}
			}
		}
		
	}
	.btn_open{
		display:block;
		position:absolute;
		top:0;
		right:-5px;
		z-index:402;
		width:32px;
		height:58px;
		@include Mix_Size_0;
		&::before{
			content:'';
			display:block;
			position:absolute;
			top:50%;
			right:10px;
			width:12px;
			height:8px;			
			transition:all .2s ease;
			transform: translateY(-50%);
			@include Icon_Arr(1.5, '000000');
		}
	}
	&.load{
		opacity:1;
	}
	&.on{
		top:56px;
		left:0;
		width:100%;
		height:auto;		
		.tit{//인기 검색어 타이틀
			display:block;
			padding:21px 21px 18px;
			border-bottom:1px solid $COL_Line_Dark;
			color:$COL_Black;
			@include Mix_Font('Head-R', 14, 18);
			span{
				margin-left:10px;
				color:$COL_Base_02;
				@include Mix_Font('Head-R', 12, 16);
			}
		}
		.list{
			height:auto;
			padding:10px 25px 15px;
			li {
				a {
					padding: 10px 0;
					font-size: 15px;
					em {
						font-size: 14px;
					}
				}
				&+li {
					margin-top: 3px;
				}
			}
		}
		.btn_open{
			&::before{
				transform: rotate(-180deg) translateY(50%);
			}
		}
	}
}
.srch_model{
	position:relative;
	top: calc(100% - 56px);
	opacity: 0;
	transition:top 0.8s ease;
	&.load {
		top: calc(100% - 319px);
		opacity: 1;
	}
}

//기획전 배너
.srch_promotion_swiper {
	padding: 0 15px;
	position: relative;
	// transition:all .8s ease;
	// margin-top: 100%;
	opacity: 0;
	overflow: hidden;
	.swiper-slide {
		width: 100%;
		.unit_img {
			padding-bottom: 0;
		}
	}
	//2개이하
	&.under {
		opacity: 1;
		padding-top: 7px;
		.unit_event {
			position: relative;
			.unit_img {
				margin-bottom: 0;
				height: 140px;
			}
			.unit_info {
				position: absolute;
				top: 20px;
				left: 20px;
				z-index: 3;
				width: 90%;
				.name {
					@include Mix_Font ('Head-R', 17, 21);
					color: $COL_White;
					@include Textshadow(0, 1px, 0.6)
				}
			}
		}
	}	
	//3개이상
	&.more {
		opacity: 1;
		padding: 0 21px 6px;
		.swiper-slide {
			width: 96px;
		}
		.unit_event {
			.unit_img {
				margin-bottom: 10px;
				height: 120px;
				img {
					opacity: 1;
				}
			}
			.unit_info {
				.name {
					@include Mix_Font ('Head-R', 13, 16);
					color: $COL_Base_03;
					padding-right: 10px;
				}
			}
		}
	}
	.swiper-pagination {
		margin-top: 15px;
	}
}
