.srch_recent{// 검색하기 3. 최근검색어
	display:none;
	// height:calc(100% + 56px);
	height:100%;
	overflow-y:auto;
	margin-top:300px;
	background:$COL_White;
	transition:margin 1s ease;
	//최근검색 기록 없음
	&_blank {
		padding-top: 50px;
		color: $COL_Base_02;
		text-align:center;
	}
	> .inner{
		padding:20px 21px 25px;
		&.sec_condition {
			padding-top: 25px;
			padding-bottom: 45px;
			background-color: $COL_Light;
			.top{
				padding-bottom: 10px;
			}
		}
	}
	.top{
		position:relative;
		padding-bottom:20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		h4.tit{
			// color:$COL_Black;
			@include Mix_Font('Head-R', 14, 18);
		}
		.txt{
			color:$COL_Base_01;
			@include Mix_Font('Head-R', 12, 18);
		}
	}
	.recent_list{// 최근 검색조건
		li{
			border-bottom: 1px solid $COL_Line_Medium;
			a {
				display: block;
				padding: 15px 40px 15px 0;
				position: relative;
				.tit {
					@include Mix_Font('head-R', 13, 16);
					color: $COL_Base_03;
				}
				.txt {
					margin-top: 3px;
					@include Mix_Font('Txt-R', 13, 20);
					color: $COL_Base_02;
					@include Mix_Ellipsis01;
				}
				.del {
					@include Mix_Del_Icon(11, 1.2, '8F8F8F', $W:20);
					@include Pos($p:absolute,$t:50%,$r:-4px);
					margin-top: -10px;
					display: flex;
					justify-content: center;
					align-items: center;
				}
			}
		}
		&.toggle{
			li:nth-child(n+6){
				display: none;
			}
			&.on{
				li {
					display: block;
				}
			}
			.btn_more{
				margin: 15px 0;
			}
		}
	}
	
}

.srch_chkbox{
	position:sticky;
	left:0;
	top:0;
	z-index:9;
	padding:0 21px 25px 21px;
	background:$COL_White;
	border-bottom: 1px solid $COL_Line_Dark
}

.recent_swiper{// 최근검색어 
	position:relative;
	overflow:hidden;
	height:auto;
	margin: -5px -3px 0;
	.list{
		align-items:flex-start;
		// transition-property:transform,height;
		// transform:translate3d(0px,0,0);
		li{
			flex-shrink:0;
			display:flex;
			flex-flow:wrap;
			height:auto;
			a, span {
				margin: 5px 3px;
			}
		}
		.btn_txt01{// 최근검색어 '전체삭제' 텍스트 버튼
			position:absolute;
			top:2px;
			right:0;
		}
		.txt{
			position:absolute;
			top:2px;
			right:0;
			color:$COL_Base_01;
			@include Mix_Font('Head-R', 12, 16);
		}
	}
	.swiper-pagination {
		margin-top:10px;
	}

}
.srch_auto{// 검색하기 4. 자동완성
	display:none;
	height: 100%;
	overflow-y:auto;
	background:$COL_White;
	ol{
		li{
			a{
				display:flex;
				align-items:center;
				justify-content:space-between;
				> span {
					color:$COL_Base_03;
				}
				.thumb {
					display: inline-block;
					width: 60px;
					height: 60px;
					margin-right: 10px;
					position: relative;
					img {
						width: 100%;
						height: auto;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
					}
				}
				.tit{
					color:$COL_Primary;
					@include Mix_Font('Head-M', 14, 18);
					flex: 1 1;
					word-break: break-word;
					em {
						display:block;
						color:$COL_Base_02;
						@include Mix_Font('Txt-R', 12, 18);
					}
				}
				.txt{
					@include Mix_Font('Head-R', 14, 18);
					word-break: break-word;
					i{
						color:$COL_Primary;
					}
				}
				.no {
					@include Mix_Font('Head-R', 13, 16);
				}
				.more{
					margin-left:3px;
					@include Mix_Size_0;
					@include Icon_Arr_Right(1.2, '666666');
				}
			}
		} 
	}
	.list_01{// 모델 추천 목록
		padding:0 21px;
		background:$COL_Light;
		li {
			a {padding: 5px 0;}
			&+li {
				border-top:1px solid $COL_Line_Medium;
			}
		}
		&.event li a {
			padding: 16px 0;
		}
		&+.list_01 li {
			border-top:1px solid $COL_Line_Medium;
		}
		&:not(.event) {
			padding-top: 5px;
		}
	}
	.list_02{// 키워드별 자동완성
		padding:0 21px;
		li{
			border-bottom:1px solid $COL_Line_Light;
			a {
				padding:16px 0;
			}
		}
	}
}
// 자동완성 모델 대표 이미지
[class^="ic_model_"]{
	&::before{
		content:'';
		display:flex;
		margin-right:10px;
		width:60px;
		height:60px;
		@include Mix_Model_Bg;
	}
}
.ic_model_01{// 모델 대표이미지 
	&::before{
		background-position:0 0;
	}
}
.ic_model_02{// 모델 대표이미지 
	&::before{
		background-position:-65px 0;
	}
}
.ic_model_03{// 모델 대표이미지 
	&::before{
		background-position:-130px 0;
	}
}
.ic_model_0001{// 모델 대표이미지 
	&::before{
		background-position:0 0;
	}
}
.ic_model_00010001{// 모델 대표이미지 
	&::before{
		background-position:-65px 0;
	}
}
.ic_model_0002{// 모델 대표이미지 
	&::before{
		background-position:-130px 0;
	}
}
.ic_model_00020001{// 모델 대표이미지 
	&::before{
		background-position:-130px 0;
	}
}
// 모델검색부터 조건 검색 영역 
/* 검색 레이어 :검색 조건 :상단 */
.srch_box{// 검색조건 box
	// border-top:1px solid #F2F2F2;
	border-bottom:1px solid $COL_Line_Medium;
	// &#schBox-03,
	// &#schBox-10{//03. 모델상세, 10 차량정보
	// 	.incont{
	// 		padding:0;
	// 	}
	// }
	.hidden {
		display: none !important;
	}
	> .tit_btn{// 검색조건 box 1. 타이틀
		position:relative;
		padding:25px 21px;
		height: 70px;
		h4.tit{
			display:inline-block;
			color:$COL_Black;
			@include Mix_Font('Head-M', 16, 20);
		}
		span.check{
			display:none;
			width:calc(100% - 90px);
			margin-left:11px;
			color:$COL_Base_02;
			@include Mix_Font('Txt-R', 13, 20);
			@include Mix_Ellipsis01;
			vertical-align:middle;
		}
		span.check2 {
			display:none;
			margin-left:11px;
			color:$COL_Base_02;
			@include Mix_Font('Txt-R', 13, 20);
			vertical-align:middle;
			& + span.check {
				margin-left: 0;
			}
		}
		&::after {
			right: 21px;
		}
	}
	&.on{
		> .incont{
			// height:auto;
			display: block;
			padding:0 21px 25px;
			.cpo_range{
				display:block;
			}
		}
		.range_chart{
			ol{
				li{
					i{
						height:100%;
						margin-top:0;
					}
				}
			}
		}
	}
	&.show {
		> .tit_btn{
			span.check,
			span.check2{
				display:inline-block;
			}
		}
	}
	&.disabled {
		.tit_btn {
			opacity: .3;
			pointer-events: none;
		}
	}
	> .incont{// 검색조건 box 2. 내용
		position: relative;
		padding:0 21px;
		// height:0;
		display: none;
		overflow:hidden;
		// transition:height .3s ease;
		.text{
			display: none;
		}
		.btn_more{
			margin-top: 20px;
		}
		.btn_area{
			padding-top:35px;
		}
		.carinfo{// 03. 모델상세, 10. 차량선택
			// .chk_rad01 + label {
			// 	color: $COL_Base_03;
			// 	@include Mix_Font('Txt-R', 15, 23);
			// }
			li + li {
				margin-top: 20px;
			}
			> li{
				em {
					padding-left: 6px;
					@include Mix_Font('Txt-R', 12, 24);
					color: $COL_Base_01;
				}
				.depth2{
					display:none;
					padding:25px 0 5px 29px;
				}
				.depth3{
					display:none;
					padding:20px 0 20px 80px;
					margin:20px -21px 0 -50px;
					background:$COL_Light;
				}
				&.on{
					.depth2{
						display:block;
						> li{
							&.on{
								.depth3{
									display:block;
								}

							}
						}
					}
				}
			}
		}
		.color{// 09. 색상계열
			display: flex;
			gap: 30px 0;
			flex-wrap: wrap;
			li{
				width:70px;
				text-align:center;
				&:nth-child(3n-1) {
					width: calc(100% - 140px);
				}
			}
			.chk_color:disabled + label {
				opacity: 0.3;
			}
			& + .notice_03 {
				padding-bottom: 25px;
			}

		}
		.option{// 11. 옵션
			dt{
				@include Mix_Font('Head_R', 14, 18);
			}
			dd{
				margin-top:15px;
				ol{// 목록
					li + li {
						margin-top: 20px;
					}
					label {
						min-height: 24px;
						@include Mix_Ellipsis(1);
					}
				}
				& + dt {
					margin-top: 35px;
				}
			}
		}
		&.no-condition{
			.text{
				display: block;
				margin: 42px 0;
				text-align: center;
				color:$COL_Base_02;
				@include Mix_Font('text-R', 14, 21);
			}
			.range_chart{
				display: none;
			}
			.ui-slider-range:first-child{
				width: 100% !important;
				left: 0% !important;
			}
			.ui-slider-handle:nth-child(2){
				left: 0 !important;
			}
			.ui-slider-handle:nth-child(3) {
				left: 100% !important;
			}
			.cpo_range::before{
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				z-index: 10;
			}
		}
	}
}

#schBox-02 .incont, 
#schBoxLayer-02 .incont {
	li:nth-child(n+7){
		display: none;
	}
	&.on li {
		display: block;
	}
}

