.srch_result{// 검색결과 리스트 화면
	padding-top:17px;
	margin-top:3px;
	.slide_product {
		position:relative;
		margin-top:40px;
		padding-top:40px;
		&::before {
			@include Mix_Space_Bar;
		}
	}
	.plp_template01 {
		margin-top: 13px;
	}
}

/*가격 슬라이더*/
.cpo_range{
	display:none;
	padding:12px 0;
	.amountArea{
		visibility:hidden;
	}
	.cpoRange{
		position:relative;
		width:calc(100% - 24px);
		height:2px;
		margin-left:12px;
		background:$COL_Line_Medium;
		.ui-slider-range{
			position:absolute;
			top:0;
			z-index:1;
			display:block;
			height:100%;
			background:$COL_Secondary;
			border:0;
		}
		.ui-slider-handle{
			z-index:2;cursor:default;-ms-touch-action:none;touch-action:none;outline:none;
			width:24px;
			height:24px;
			background:$COL_White;
			border:2px solid $COL_Secondary;
			border-radius:100%;
			position:absolute;
			top:-11px;
			&:nth-child(2){
				left: 0;
				margin-left:-12px;
			}
			&:nth-child(3){
				left: 100%;
				margin-left:-12px;
			}
		}
		.amountArea{
			visibility:hidden;
		}
		.min_no,
		.max_no{
			display:block;
			width:100px;
			position:absolute;
			top:21px;
			color:$COL_Base_01;
			@include Mix_Font('Txt-R', 12, 18);
		}
		.min_no{
			left:-11px;
			text-align:left;
		}
		.max_no{
			right:-11px;
			text-align:right;
		}
	}
}

/* 가격 차트 */
// #rangePrice{
// 	margin-top:-10px;
// }
.range_chart{
	width:100%;
	height:120px;
	position:relative;
	overflow:hidden;
	margin-bottom: -8px;
	> ol{
		width:100%;
		@include Mix_Clear;
		li{
			display:flex;
			width:5%;
			height:120px;
			margin:0 auto;
			text-indent: -99999px;
			transition: height 1s;
			flex-direction: column;
			align-items: center;
			justify-content: flex-end;
			&.on{
				.bar{
					i{
						background:$COL_Secondary;
					}
				}
			}
			float:left;
			.bar{
				width:2px;
				i{
					display:block;
					width:2px;
					height:1%;
					transition: margin 1s;
					background: $COL_Line_Dark;
					margin-top:120px;
				}
			}

		}
	}
}

@keyframes Ani_Price_Chart{
	from {
		max-height:0;
	}
	to {
		max-height:100%;
	}
}

.inp_search{
	.txt {
		&::placeholder{
			color:$COL_Base_01;
		}
	}
	.ico_delete{
		position:absolute;
		top:50%;
		right:85px;
		transform:translateY(-54%);
		display:none;
	}
}